.logo{
    width: 25%;
    margin-left: auto;
    margin-right: auto;
    margin-top: -50px;
    animation: rotation 2s infinite ease;
}
@media (max-width: 1000px) {
    .logo{
        width: 100%;
    }
}
.logoDiv{
    display: flex;
    justify-content: center;
}

